* {
	padding: 0;
	margin: 0;
}

body {
	background: #f1f1f1;
	font-family: "Segoe UI";
}

h1 {
	font-size: 50px;
}

h3 {
	font-size: 18px;
}

p {
	font-size: 14px;
}

img {
	user-select: none;
}


/*all*/
.container {
	margin: 0 auto;
	max-width: 1150px;
	width: 100%;
	position: relative;
}

.title {
	font-weight: 800;
}

.sub-title {
	font-weight: bold;
}

/*header*/
.header {
	background-image: url("/src/images/header.png");
	background-size: cover;
	background-position: center;
	height: 350px;
}

.header .container {
	height: 100%;
}

.header .header-nav {
	position: absolute;
	right: 30px;
	top: 10px;
}

.header .header-nav a {
	margin: 0 0 0 20px;
}

.header .header-content {
	position: absolute;
	bottom: 30px;
}

.header .header-content h1 {
	color: #fff;
}

.header .header-content h3 {
	color: #cccccc;
}

/*content*/
.content {
	margin: 70px 0 0 0;
}

.content .hot-title {
	display: flex;
}

.content .hot-title .icon {
	margin: 0 15px 0 0;
}

.content .hot-title .text h3 {
	color: #333;
}

.content .hot-title .text p {
	color: #676767;
}

.content .hot-cotent {
	margin: 30px 0 0 0;
}

.content .hot-cotent .hot-card {
	width: calc(100% / 3 - 55px * 2);
	margin: 0px 55px 50px 55px;
	float: left;
}

.content .hot-cotent .hot-card .hot-card-header {
	height: 150px;
	border-radius: 10px;
	background-color: #757575;
	background-size: cover;
	background-position: center;
}

.content .hot-cotent .hot-card  .title {
	margin: 10px 0 0 0;
}

.content .hot-cotent .hot-card  h5 {
	color: #757575;
}